<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="dialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <el-table :data="rolelist" border stripe>
        <el-table-column type="expand">
          <template v-slot="scope">
            <el-row :class="['bd-bottom','vcenter',index1==0?'bd-top':'']" v-for="(item1,index1) in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)" >{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row :class="[index2==0?'':'bd-top','vcenter']" v-for="(item2,index2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag closable @close="removeRightById(scope.row,item2.id)" type="success">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag closable @close="removeRightById(scope.row,item3.id)" type="warning" v-for="(item3) in item2.children" :key="item3.id">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="rolename" label=""></el-table-column>
        <el-table-column prop="roledetail" label=""></el-table-column>
        <el-table-column label="" width="450px">
          <template v-slot="scope">
            <el-button type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row)"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="deleteUser(scope.row)"></el-button>
            <el-button type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        :model="addUserForm"
        ref="addUserFormRef"
        label-width="70px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="rolename">
          <el-input v-model="addUserForm.rolename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roledetail">
          <el-input v-model="addUserForm.roledetail"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editUserForm"
        :rules="editUserFormRules"
        ref="editUserFormRef"
        label-width="70px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="rolename">
          <el-input v-model="editUserForm.rolename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roledetail">
          <el-input v-model="editUserForm.roledetail"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      @close="setRightDialogClosed"
      width="50%"
      >
      <el-tree
        :data="rightslist"
        node-key="id"
        default-expand-all
        show-checkbox>
      </el-tree>
      <div slot="footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rolelist: [],
      dialogVisible: false,
      editDialogVisible: false,
      editUserForm: {},
      editUserFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 1,
            max: 10,
            message: ' 1  10 ',
            trigger: 'blur'
          }
        ],
        detail: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 1,
            max: 10,
            message: ' 1  10 ',
            trigger: 'blur'
          }
        ]
      },
      addUserForm: {
        id: 0,
        name: '',
        detail: ''
      },
      addUserFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 2,
            max: 10,
            message: ' 2  10 ',
            trigger: 'blur'
          }
        ],
        value: [
          { required: true, message: 'value', trigger: 'blur' },
          {
            require: true,
            trigger: 'blur'
          }
        ]
      },
      setRightDialogVisible: false,
      rightslist: [
        {
          id: '1',
          label: '',
          children: [{
            id: '101',
            label: ''
          }]
        },
        {
          id: '2',
          label: '',
          children: [{
            id: '201',
            label: ''
          },
          {
            id: '202',
            label: ''
          }]
        },
        {
          id: '3',
          label: '',
          children: [{
            id: '301',
            label: ''
          },
          {
            id: '302',
            label: ''
          }]
        },
        {
          id: '4',
          label: '',
          children: [{
            id: '401',
            label: ''
          }]
        },
        {
          id: '5',
          label: '',
          children: [{
            id: '501',
            label: ''
          },
          {
            id: '502',
            label: ''
          }]
        }
      ],
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      defKeys: [],
      roleId: ''
    }
  },
  methods: {
    async getRoleList () {
      const { data } = await this.$http.get('role/findAll')
      // if (data.status !== 200) {
      //   return this.$message.error(data.msg)
      // }
      console.log(data)
      this.rolelist = data
    },
    addDialogClosed () {
      this.$refs.addUserFormRef.resetFields()
    },
    addUser () {
      this.$refs.addUserFormRef.validate(async valid => {
        if (!valid) return
        const data = await this.$http.post('role/addRole', this.addUserForm)
        if (data.status !== 200) {
          this.$message.error('')
        }
        // this.addUserForm.id = this.addUserForm.id + 1
        // // service.addCourse(this.addUserForm)
        // const adddata = {
        //   name: this.addUserForm.name,
        //   value: this.addUserForm.value
        // }
        // this.userlist.push(adddata)
        this.$message.success('')
        this.dialogVisible = false
        this.getRoleList()
      })
    },
    async showEditDialog (id) {
      // const { data } = await this.$http.get(`users/${id}`)
      // if (data.meta.status !== 200) {
      //   this.$message.error('')
      // }
      console.log(id)
      this.editUserForm = id
      this.editDialogVisible = true
    },
    editDialogClosed () {
      this.$refs.editUserFormRef.resetFields()
    },
    editUser () {
      this.$refs.editUserFormRef.validate(async valid => {
        if (!valid) return
        const data = await this.$http.post('role/updateRole', this.editUserForm)
        if (data.status !== 200) {
          this.$message.error('')
        } else {
          this.$message.success('')
        }
        this.editDialogVisible = false
        this.getRoleList()
      })
    },
    async deleteUser (id) {
      // this.$confirm(', ?', '', {
      //   confirmButtonText: '',
      //   cancelButtonText: '',
      //   type: 'warning'
      // })
      // async () => {
      console.log(id)
      const data = await this.$http.post(`role/deleteRole?roleid=${id.roleid}`)
      if (data.status !== 200) {
        this.$message.error('')
      } else {
        this.$message({
          type: 'success',
          message: '!'
        })
      }
      console.log(data)
      this.getRoleList()
      // }
      // }
      // .catch(() => {
      //   this.$message({
      //     type: 'info',
      //     message: ''
      //   })
      // })
    },
    removeRightById (role, rightId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if (data.meta.status !== 200) {
          return this.$message.error(data.meta.msg)
        }
        this.$message({
          type: 'success',
          message: '!'
        })
        role.children = data.data
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    async showSetRightDialog (role) {
      this.roleId = role.id
      const data = await this.$http.get('power/findAll')
      console.log(data)
      if (data.status !== 200) {
        return this.$message.error(data.meta.msg)
      }
      // this.rightslist = data.data
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    getLeafKeys (node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    setRightDialogClosed () {
      this.defKeys = []
    },
    async allotRights () {
      // const keys = [
      //   ...this.$refs.treeRef.getCheckedKeys(),
      //   ...this.$refs.treeRef.getHalfCheckedKeys()
      // ]
      // const idStr = keys.join(',')
      // const { data } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      // if (data.meta.status !== 200) {
      //   return this.$message.error(data.meta.msg)
      // }
      this.$message.success('')
      this.setRightDialogVisible = false
      this.getRoleList()
    }
  },
  created () {
    this.getRoleList()
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bd-top {
  border-top: 1px solid #eee;
}

.bd-bottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>
