<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-table :data="rightsList" border stripe style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="powername" label=""></el-table-column>
        <el-table-column prop="powerlujin" label=""></el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <el-tag v-if="scope.row.powerlevel===''"></el-tag>
            <el-tag v-if="scope.row.powerlevel===''" type="success"></el-tag>
            <el-tag v-if="scope.row.powerlevel===''" type="info"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rightsList: []
    }
  },
  methods: {
    async getRightsList () {
      const { data } = await this.$http.get('power/findAll')
      // if (data.meta.status !== 200) {
      //   return this.$message.error(data.meta.msg)
      // }
      this.rightsList = data
    }
  },
  created () {
    this.getRightsList()
  }
}
</script>

