<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            clearable
            @clear="getCourseList"
            v-model="queryInfo.query"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchcourse"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="dialogVisible = true"
            ></el-button>
        </el-col>
      </el-row>
      <el-table :data="courselist" border stripe style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="coursename" label=""></el-table-column>
        <el-table-column prop="courseinfo" label=""></el-table-column>
        <el-table-column prop="classid" label=""></el-table-column>
        <el-table-column prop="semester" label=""></el-table-column>
        <el-table-column prop="school.schoolname" label="">
          <template v-slot="scope">
            {{scope.row.school.schoolname}}/{{scope.row.school.xueyuan.xueyuanname}}/{{scope.row.school.zhuanye.zhuanyename}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <!--  -->
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUser(scope.row)"
            ></el-button>
            <!--  -->
            <el-tooltip
              :enterable="false"
              effect="dark"
              content=""
              placement="top-start"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setSchool(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        :model="addCourseForm"
        :rules="addCourseFormRules"
        ref="addCourseFormRef"
        label-width="90px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="coursename">
          <el-input v-model="addCourseForm.coursename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="courseinfo">
          <el-input v-model="addCourseForm.courseinfo"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classid">
          <el-input v-model="addCourseForm.classid"></el-input>
        </el-form-item>
        <el-form-item label="" prop="semester">
          <el-input v-model="addCourseForm.semester"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCourse"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editCourseForm"
        :rules="editCourseFormRules"
        ref="editCourseFormRef"
        label-width="90px"
        class="demo-ruleForm"
      >
        <el-form-item label="">
          <el-input v-model="editCourseForm.coursename" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="courseinfo">
          <el-input v-model="editCourseForm.courseinfo"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classid">
          <el-input v-model="editCourseForm.classid"></el-input>
        </el-form-item>
        <el-form-item label="" prop="semester">
          <el-input v-model="editCourseForm.semester"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCourse"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setSchoolDialogVisible"
      @close="setSchoolDialogClosed"
      width="50%"
    >
      <div>
        <p>:{{ courseInfo.coursename }}</p>
        <p>:{{ school }}</p>
        <p>:
          <!-- <el-select v-model="selectSchoolId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.roleid"
              :label="item.rolename"
              :value="item.roleid">
            </el-option>
          </el-select> -->
          <el-cascader
              v-model="selectSchoolId"
              :options="schoolList"
              @change="handleChange"></el-cascader>
        </p>
      </div>
      <div slot="footer">
        <el-button @click="setSchoolDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveSchoolInfo"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      courselist: [],
      // school: {
      //   schoolid: 2,
      //   schoolname: '',
      //   schoolxueyuan: [{
      //     id: 5,
      //     name: '',
      //     schoolzhuanye: [{
      //       id: 8,
      //       name: ''
      //     }]
      //   },
      //   {
      //     id: 6,
      //     name: '',
      //     schoolzhuanye: [{
      //       id: 9
      //       name: ''
      //     }]
      //   }]
      // }
      total: 0,
      dialogVisible: false,
      editDialogVisible: false,
      addCourseForm: {
        coursename: '',
        courseinfo: '',
        classid: '',
        semester: '',
        school: {
          schoolid: 1
        }
      },
      setSchoolDialogVisible: false,
      addCourseFormRules: {
        coursename: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 2,
            max: 10,
            message: ' 2  10 ',
            trigger: 'blur'
          }
        ],
        courseinfo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        classid: [
          { required: true, message: '', trigger: 'blur' }
        ],
        semester: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editCourseForm: {},
      editCourseFormRules: {
        courseinfo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        classid: [
          { required: true, message: '', trigger: 'blur' }
        ],
        semester: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      courseInfo: '',
      schoolList: [],
      selectSchoolId: [],
      school: ''
    }
  },
  methods: {
    async getCourseList () {
      const { data } = await this.$http.get('course/findAll', {
        params: this.queryInfo
      })
      console.log(data)
      this.courselist = data
      this.total = data.length
    },
    searchcourse () {
      const newcourselist = []
      const list = this.courselist
      for (let i = 0; i < list.length; i++) {
        if (list[i].coursename === this.queryInfo.query) {
          newcourselist.push(list[i])
        }
      }
      this.courselist = newcourselist
      this.total = newcourselist.length
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getCourseList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getCourseList()
    },
    addDialogClosed () {
      this.$refs.addCourseFormRef.resetFields()
    },
    addCourse () {
      this.$refs.addCourseFormRef.validate(async valid => {
        if (!valid) return
        // const newuser = {
        //   username: this.addCourseForm.username,
        //   password: this.addCourseForm.password,
        //   email: this.addCourseForm.email,
        //   telephone: this.addCourseForm.telephone,
        //   role: {
        //     roleid: 1,
        //     rolename: ''
        //   }
        // }
        const data = await this.$http.post('course/addCourse', this.addCourseForm)
        console.log(data)
        if (data.status !== 200) {
          this.$message.error('')
        } else {
          this.$message.success('')
        }
        this.dialogVisible = false
        this.getCourseList()
      })
    },
    async showEditDialog (id) {
      // const { data } = await this.$http.get(`users/${id}`)
      // if (data.meta.status !== 200) {
      //   this.$message.error('')
      // }
      console.log(id)
      this.editCourseForm = id
      this.editDialogVisible = true
    },
    editDialogClosed () {
      this.$refs.editCourseFormRef.resetFields()
    },
    editCourse () {
      this.$refs.editCourseFormRef.validate(async valid => {
        if (!valid) return
        const data = await this.$http.post('course/updateCourse', this.editCourseForm)
        console.log(data)
        if (data.status !== 200) {
          this.$message.error('')
        } else {
          this.$message.success('')
        }
        this.editDialogVisible = false
        this.getCourseList()
      })
    },
    async deleteUser (id) {
      // this.$confirm(', ?', '', {
      //   confirmButtonText: '',
      //   cancelButtonText: '',
      //   type: 'warning'
      // })
      // async () => {
      const data = await this.$http.post(`course/deleteCourse?courseid=${id.courseid}`)
      if (data.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      console.log(data)
      this.getCourseList()
      // }
      // }
      // .catch(() => {
      //   this.$message({
      //     type: 'info',
      //     message: ''
      //   })
      // })
    },
    async setSchool (courseInfo) {
      const { data } = await this.$http.get('school/findAll')
      // if (data.status !== 200) {
      //   return this.$message.error(data.msg)
      // }
      // const list1 = []
      const list2 = []
      // const obj = {
      //   value: '',
      //   label: '',
      //   children: [{
      //     value: '',
      //     label: '',
      //     children: [{
      //       value: '',
      //       label: ''
      //     }]
      //   }]
      // }
      for (let i = 0; i < data.length; i++) {
        const obj = {
          value: '',
          label: '',
          children: [{
            value: '',
            label: '',
            children: [{
              value: '',
              label: ''
            }]
          }]
        }
        obj.value = data[i].schoolid
        obj.label = data[i].schoolname
        obj.children[0].value = data[i].xueyuan.xueyuanid
        obj.children[0].label = data[i].xueyuan.xueyuanname
        obj.children[0].children[0].value = data[i].zhuanye.zhuanyeid
        obj.children[0].children[0].label = data[i].zhuanye.zhuanyename
        list2.push(obj)
      }
      // console.log(userInfo)
      this.schoolList = list2
      console.log(this.schoolList)
      this.courseInfo = courseInfo
      this.school = this.courseInfo.school.schoolname + '/' + this.courseInfo.school.xueyuan.xueyuanname + '/' + this.courseInfo.school.zhuanye.zhuanyename
      // console.log(this.courseInfo)
      this.setSchoolDialogVisible = true
    },
    async saveSchoolInfo () {
      if (!this.selectSchoolId) {
        return this.$message.error('')
      }
      console.log(this.selectSchoolId)
      console.log(this.courseInfo)
      this.courseInfo.school.schoolid = this.selectSchoolId[0]
      this.courseInfo.school.xueyuan.xueyuanid = this.selectSchoolId[1]
      this.courseInfo.school.zhuanye.zhuanyeid = this.selectSchoolId[2]
      console.log(this.courseInfo)
      const { data } = await this.$http.post('course/updateCourse', this.courseInfo)
      // if (data.status !== 200) {
      //   return this.$message.error(data.msg)
      // }
      console.log(data)
      this.$message.success('')
      this.getCourseList()
      this.setSchoolDialogVisible = false
    },
    setSchoolDialogClosed () {
      this.selectSchoolId = []
      this.courseInfo = {}
    },
    handleChange (selectSchoolId) {
      console.log(selectSchoolId)
    }
  },
  created () {
    const data = this.$http.get('api/parsejwt')
    console.log(data)
    this.getCourseList()
  }
}
</script>

<style lang="less" scoped></style>
