<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            clearable
            @clear="getUserList"
            v-model="queryInfo.query"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchUser"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="dialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <el-table :data="userlist" border stripe style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="telephone" label=""></el-table-column>
        <el-table-column prop="role.rolename" label=""></el-table-column>
        <el-table-column label="">
          <!--  -->
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUser(scope.row)"
            ></el-button>
            <!--  -->
            <el-tooltip
              :enterable="false"
              effect="dark"
              content=""
              placement="top-start"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        :model="addUserForm"
        :rules="addUserFormRules"
        ref="addUserFormRef"
        label-width="90px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="realname">
          <el-input v-model="addUserForm.realname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addUserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="addUserForm.telephone"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editUserForm"
        :rules="editUserFormRules"
        ref="editUserFormRef"
        label-width="70px"
        class="demo-ruleForm"
      >
        <el-form-item label="">
          <el-input v-model="editUserForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="editUserForm.telephone"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRoleDialogVisible"
      @close="setRoleDialogClosed"
      width="50%"
    >
      <div>
        <p>:{{ userInfo.username }}</p>
        <p>:{{ role }}</p>
        <p>:
          <el-select v-model="selectRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.roleid"
              :label="item.rolename"
              :value="item.roleid">
            </el-option>
          </el-select>
        </p>
      </div>
      <div slot="footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    // 
    var checkEmail = (rule, value, cb) => {
      const regEail = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/
      if (regEail.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    // 
    var checkTelephone = (rule, value, cb) => {
      const regMobile = /^[1][3,4,5,7,8][0-9]{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userlist: [],
      viruserlist: [
        { userid: '101', username: 'cai', password: 'cai', email: 'cai@163.com', telephone: '13859697981', role: { roleid: 1, roledetail: 'superadmin', rolename: '' } },
        { userid: '102', username: 'wang', password: 'wang', email: 'wang@163.com', telephone: '13859697982', role: { roleid: 2, roledetail: 'teacher', rolename: '' } },
        { userid: '103', username: 'du', password: 'du', email: 'du@163.com', telephone: '13858697983', role: { roleid: 3, roledetail: 'assitant', rolename: '' } },
        { userid: '104', username: 'lin', password: 'lin', email: 'lin@163.com', telephone: '13859697984', role: { roleid: 4, roledetail: 'student', rolename: '' } }
      ],
      total: 0,
      dialogVisible: false,
      editDialogVisible: false,
      addUserForm: {
        username: '',
        realname: '',
        password: '',
        email: '',
        telephone: '',
        role: {
          roleid: 1,
          rolename: ''
        }
      },
      setRoleDialogVisible: false,
      addUserFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        realname: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 2,
            max: 5,
            message: ' 2  5 ',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            require: true,
            min: 6,
            max: 15,
            message: ' 6  15 ',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        telephone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkTelephone, trigger: 'blur' }
        ]
      },
      editUserForm: {},
      editUserFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        telephone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkTelephone, trigger: 'blur' }
        ]
      },
      userInfo: '',
      rolesList: [],
      selectRoleId: '',
      role: '',
      id: 100
    }
  },
  methods: {
    async getUserList () {
      const { data } = await this.$http.get('api/parsejwt', {
      })
      console.log(data.message.success)
      if (data.message.success !== true) {
        this.$message.error('')
        this.$router.push('/login')
      } else {
        const { data } = await this.$http.get('user/findAll', {
          params: this.queryInfo
        })
        this.userlist = data
        this.total = data.length
      }
    },
    searchUser () {
      const newuserlist = []
      const list = this.userlist
      for (let i = 0; i < list.length; i++) {
        if (list[i].username === this.queryInfo.query) {
          newuserlist.push(list[i])
        }
      }
      this.userlist = newuserlist
      this.total = newuserlist.length
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    addDialogClosed () {
      this.$refs.addUserFormRef.resetFields()
    },
    addUser () {
      this.$refs.addUserFormRef.validate(async valid => {
        if (!valid) return
        const newuser = {
          username: this.addUserForm.username,
          password: this.addUserForm.password,
          email: this.addUserForm.email,
          telephone: this.addUserForm.telephone,
          realname: this.addUserForm.realname,
          role: {
            roleid: 1,
            rolename: ''
          },
          school: {
            schoolid: 1,
            schoolname: '',
            xueyuan: {
              xueyuanid: 1,
              xueyuanname: ''
            },
            zhuanye: {
              zhuanyeid: 1,
              zhuanyename: ''
            }
          }
        }
        const data = await this.$http.post('user/addUser', newuser)
        console.log(data)
        if (data.status !== 200) {
          this.$message.error('')
        } else {
          this.$message.success('')
        }
        this.dialogVisible = false
        this.getUserList()
      })
    },
    async showEditDialog (id) {
      // const { data } = await this.$http.get(`users/${id}`)
      // if (data.meta.status !== 200) {
      //   this.$message.error('')
      // }
      console.log(id)
      this.editUserForm = id
      this.editDialogVisible = true
    },
    editDialogClosed () {
      this.$refs.editUserFormRef.resetFields()
    },
    editUser () {
      this.$refs.editUserFormRef.validate(async valid => {
        if (!valid) return
        const data = await this.$http.post('user/updateUser', this.editUserForm)
        if (data.status !== 200) {
          this.$message.error('')
        } else {
          this.$message.success('')
        }
        this.editDialogVisible = false
        this.getUserList()
      })
    },
    async deleteUser (id) {
      // this.$confirm(', ?', '', {
      //   confirmButtonText: '',
      //   cancelButtonText: '',
      //   type: 'warning'
      // })
      // async () => {
      const data = await this.$http.post(`user/deleteUser?userid=${id.userid}`)
      if (data.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      console.log(data)
      this.getUserList()
      // }
      // }
      // .catch(() => {
      //   this.$message({
      //     type: 'info',
      //     message: ''
      //   })
      // })
    },
    async setRole (userInfo) {
      const { data } = await this.$http.get('role/findAll')
      // if (data.status !== 200) {
      //   return this.$message.error(data.msg)
      // }
      // const list1 = []
      // for (let i = 0; i < data.length; i++) {
      //   list1[i] = data[i].rolename
      // }
      // console.log(userInfo)
      this.rolesList = data
      this.userInfo = userInfo
      this.role = userInfo.role.rolename
      console.log(this.userInfo)
      this.setRoleDialogVisible = true
    },
    async saveRoleInfo () {
      if (!this.selectRoleId) {
        return this.$message.error('')
      }
      console.log(this.selectRoleId)
      this.userInfo.role.roleid = this.selectRoleId
      const data = await this.$http.post('user/updateUser', this.userInfo)
      if (data.status !== 200) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      console.log(data)
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    setRoleDialogClosed () {
      this.selectRoleId = ''
      this.userInfo = {}
    }
  },
  created () {
    const data = this.$http.get('api/parsejwt')
    console.log(data)
    this.getUserList()
  }
}
</script>

<style lang="less" scoped></style>
